let typeArr=['支出','收入','报销','信用卡借贷'];
let account_typeArr=['现金','支付宝','微信','信用卡'];
export default {
	data() {
		return {
			swiperheight:500,
			tabIndex:0,
			account_typeArr: account_typeArr,
			remark:"",
			typeArr:typeArr,
			type:0,
			account_type:0,
			category_id:0,
			time:"",
			category:{
				id:0,     // 当前选中分类id
				title:"", // 当前选中分类名称
				range:[], // 可选项
				list:[]   // 服务端获取到的分类列表
			}
		}
	},
	computed: {
		startDate() {
			return this.getDate('start');
		},
		endDate() {
			return this.getDate('end');
		}
	},
	onLoad(e) {
		// 获取分类
		this.getCategory();
	},
	methods: {
		async getCategory(){
			try{
				let [err,res] = await this.$http.get('/category');
				if (!this.$http.errorCheck(err,res)) return;
				let list = res.data.data.list;
				let arr = [] , arr2 = [];
				for (let i = 0; i < list.length; i++) {
					arr.push(list[i].name);
					arr2.push({
						id:list[i].id,
						name:list[i].name
					})
				}
				this.category.range = arr;
				this.category.list = list;
			}catch(e){
				return;
			}
		},
		changeCategory(e){
			// 当前选中的id
			this.category.id = this.category.list[e.target.value].id;
			console.log(this.category.id);
			// 当前选中的名称
			this.category.title = this.category.list[e.target.value].name;
		},
		//提交
		async submit(){
			uni.showLoading({ title: '发布中...', mask: true });
			try{
				let [err,res] = await this.$http.post('/record/add',{
					money:this.money,
					type:this.type,
					account_type:this.account_type,
					time:this.time,
					category_id:this.category.id,
					remark:this.remark
				},{
					token:true,
					checkToken:true,
					checkAuth:true
				});
				if(!this.money){
					return uni.showToast({ title: '请输入金额', icon:"none" });
				}
				if(!this.category.id){
					return uni.showToast({ title: '请选择记账分类', icon:"none" });
				}
				if(!this.time){
					return uni.showToast({ title: '请选择记账时间', icon:"none" });
				}
				// 发布失败
				if (!this.$http.errorCheck(err,res)) {
					return uni.hideLoading();
				}
				// 发布成功
				uni.hideLoading();
				uni.showToast({
					title: '发布成功！'
				});
				uni.$emit('updateData',{ 
					type:"updateList",
					data:res.data.data.detail
				});
				console.log(res.data.data);
				uni.navigateBack({ delta: 1 });
			}catch(e){
				return;
			}
		},
		// tabbar点击事件
		tabtap(index){
			this.tabIndex=index;
		},
		// 滑动事件
		tabChange(e){
			this.tabIndex=e.detail.current;
		},
		// 修改时间
		bindDateChange(e) {
			this.time = e.target.value
		},
		// 单列选择
		changeOne(val){
			let arr=[];
			switch (val){
				case 'type':
				arr=typeArr;
					break;
				case 'account_type':
				arr=account_typeArr;
					break;
					
			}
			uni.showActionSheet({
				itemList: arr,
				success: res => {
					switch (val){
						case 'type':
						this.type=res.tapIndex;
							break;
						case 'account_type':
						this.account_type=res.tapIndex;
							break;
					}
				},
			});
		},
		getDate(type) {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();
		
			if (type === 'start') {
				year = year - 60;
			} else if (type === 'end') {
				year = year + 2;
			}
			month = month > 9 ? month : '0' + month;;
			day = day > 9 ? day : '0' + day;
			return `${year}-${month}-${day}`;
		}
	},
}
